"use client";

import { useState } from "react";
import { supabase } from "../lib/supabaseClient"; // relative import

type Props = {
  bucket: string;
};

export default function FileUploader({ bucket }: Props) {
  const [file, setFile] = useState<File | null>(null);
  const [uploading, setUploading] = useState(false);
  const [message, setMessage] = useState<string | null>(null);
  const [signedUrl, setSignedUrl] = useState<string | null>(null);

  const uploadFile = async () => {
    if (!file) return;

    setUploading(true);
    setMessage(null);
    setSignedUrl(null);

    // create a unique file name
    const filePath = `${Date.now()}-${file.name}`;

    // Upload to private bucket
    const { error } = await supabase.storage
      .from(bucket)
      .upload(filePath, file);

    if (error) {
      setMessage(`❌ Upload failed: ${error.message}`);
      setUploading(false);
      return;
    }

    setMessage("✅ File uploaded successfully!");

    // Generate a signed URL (valid 60 seconds)
    const { data, error: urlError } = await supabase.storage
      .from(bucket)
      .createSignedUrl(filePath, 60);

    if (urlError) {
      setMessage(`❌ Signed URL error: ${urlError.message}`);
    } else {
      setSignedUrl(data.signedUrl);
    }

    setUploading(false);
  };

  return (
    <div style={{ border: "1px solid #ccc", padding: "1rem", width: 350 }}>
      <input
        type="file"
        onChange={(e) => setFile(e.target.files?.[0] ?? null)}
      />

      <button
        onClick={uploadFile}
        disabled={!file || uploading}
        style={{ marginTop: "0.5rem" }}
      >
        {uploading ? "Uploading..." : "Upload"}
      </button>

      {message && <p style={{ marginTop: "0.5rem" }}>{message}</p>}

      {signedUrl && (
        <div style={{ marginTop: "0.5rem" }}>
          <p>Preview URL (expires in 60s):</p>
          <a href={signedUrl} target="_blank" rel="noopener noreferrer">
            Open File
          </a>
        </div>
      )}
    </div>
  );
}
